
import { defineComponent, ref, onMounted } from "vue";
import type from "../store/type";
import { useStore, mapState } from "vuex";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/swiper-bundle.css";
import "swiper/swiper.min.css";
import "swiper/components/lazy/lazy.min.css";
import SwiperCore, { Lazy, Autoplay } from "swiper";

// install Swiper modules
SwiperCore.use([Lazy, Autoplay]);

export default defineComponent({
  name: "musicList",
  data() {
    return {
      swiperOptions: {},
    };
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper;
    },
    ...mapState(["musicList"]),
  },
  methods: {
    handle() {
      console.log(this.countFormat);
    },
    countFormat(value: number) {
      if (value > 10000) return `${(value / 10000).toFixed(2)}`;
      else return value;
    },
  },
  setup() {
    let store = useStore();
    let isShowed = ref(false);
    let imgUrlArr = ref([]);

    onMounted(async () => {
      await store.dispatch(type.actions.getMusicList);
      console.log(store.state.musicList);
      imgUrlArr.value = store.state.musicList.map((item: any) => {
        return item.picUrl;
      });
      console.log("----mount----");
      console.log(imgUrlArr);

      isShowed.value = true;
    });

    return {
      isShowed,
      store,
      imgUrlArr,
    };
  },
});
