
import { defineComponent } from "vue";

export default defineComponent({
  name: "musicListTopContent",
  props: ["playlist"],
  methods: {
    countFormat(value: number) {
      if (value > 10000) return `${(value / 10000).toFixed(2)}`;
      else return value;
    },
  },
  setup() {
    return {};
  },
});
