
import { defineComponent } from "vue"
import {mapGetters,mapState} from 'vuex'

export default defineComponent({
  name: "playController",
  computed : {
      ...mapGetters(['currentPlayImgUrl']),
      ...mapState({
          'playItemName': (state:any)=>state.playlist.tracks[state.currentPlaying].al.name,
      })
      
  },
  setup() {
    return {};
  },
});
