import axios from "axios";

// 
axios.interceptors.response.use(function (response) {
    // 2xx 
    // 
    return response;
}, function (error) {
    //  2xx 
    // 
    return Promise.reject(error);
});


export  function getMusicListInfo (id:number) {
    let ret;
    ret=  axios.get('/playlist/detail',{
        params:{
            id:id
        }
    });
    console.log(ret);
    
    return ret;
};
